.icon-bar {
  width: 40px;
  position: fixed;
  bottom: 0px;
  left: 40px;
  right: auto;
  z-index: 10;
  color: var(--light-slate);
  margin-top: 120px;
}
/* .icon-bar{
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    margin: 0px;
    padding: 0px;
    list-style: none;
} */
.icon-bar a {
  text-decoration: none;
  text-decoration-skip-ink: auto;
  color: rgb(255, 255, 255);
  position: relative;
  transition: var(--transition);
  margin: 10px 0;
}
svg.feather {
  margin: 10px 10px;
  fill: none;
}
svg.feather path, svg.feather polygon, svg.feather line, svg.feather polyline {
  color: white;
}

svg.feather:hover {
  transform: scale(1.5);
}
.icon-bar::after,
.mail-bar::after {
  content: "";
  display: block;
  width: 1px;
  height: 90px;
  margin: 0px auto;
  background-color: rgb(255, 255, 255);
}

.right-bar {
  width: 40px;
  position: fixed;
  bottom: 0px;
  left: auto;
  right: 40px;
  z-index: 10;
  color: var(--light-slate);
}
.mail-bar {
  display: flex;
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  position: relative;
}
.mail-bar a {
  margin: 20px auto;
  padding: 3px;
  font-family: var(--font-mono);
  font-size: var(--fz-xxs);
  line-height: var(--fz-lg);
  letter-spacing: 0.2em;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}

@media only screen and (max-width: 600px) {
  
  .right-bar  {
      position: relative;
    transform: rotate(-90deg);
    left: 0;
    right: 0;
    width: 100%;
  }
  .icon-bar{
    position: relative;
    transform: rotate(90deg);
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  svg.feather{
    transform: rotate(270deg);

  }
  .icon-bar::after, .mail-bar::after{
      display: none;
  }
}

@media (max-width: 992px) {
  .particles-bg-canvas-self {
    display: none;
  }
}
